.countDown {
    position: absolute;
    top: 22.5%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 7rem;
    color: white;

    &::before {
        content: '';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 4rem;
        border-radius: 50%;
        border: solid 0.25rem white;
        box-shadow: 0 0 0.4rem 0.7rem #333;
        background-color: #333;
    }
}