.typingScene {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}

.typingWindow {
    position: fixed;
    box-sizing: border-box;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 2rem 4rem;
    width: 52rem;
    border-radius: 0.4rem;
    background-color: #333;
}

.typingWindow>.divs {
    height: 16rem;
}

.typingWindowButton {
    margin-right: 1.5em;
    padding-top: 0.04em;
    width: 8em;
    height: 1.6em;
    border-radius: 0.8em;
    background-color: #6c6c6c;
    font-size: 1.17rem;
    font-weight: 600;
    color: #ccc;

    &:hover,
    &:focus {
        background-color: rgb(241, 154, 48);
        color: #fff;
    }
}