$portfolioImgSize: 14rem;

.work-area {
    text-align: center;
}

.work-button {
    display: grid;
    grid-template-columns: $portfolioImgSize auto;
    overflow: hidden;
    margin: auto;
    max-width: 50rem;
    box-shadow: 0.1rem 0.1rem 1rem #ddd;
    text-align: left;
    border-radius: 0.4rem;
    background-color: rgb(252, 218, 115);
    transition: background-color 0.2s;

    &:hover,
    &:focus {
        background-color: rgb(255, 210, 77);
    }
}

.work-img {
    width: $portfolioImgSize;
    height: $portfolioImgSize;
}

.work-text-area {
    margin: auto;
    padding: 0 2.5rem;
    text-shadow: 0.01em 0.02em orange;
}

.work-title {
    margin-top: 0.1rem;
    margin-bottom: 2rem;
    font-size: 1.2rem;
    line-height: 1.6em;
}

.work-overview {
    line-height: 1.7em;
}

@media screen and (max-width: 959px) {
    .work-button {
        display: block;
        margin: 0 5%;
        padding: 2em;
    }

    .work-img {
        display: none;
    }

    .work-text-area {
        padding: 0;
    }
}

@media screen and (max-width: 480px) {
    .work-button {
        pointer-events: none;
        margin: 0;
        padding: 2rem 1em;
        border-radius: 0;
        box-shadow: none;
    }
}