.resultTitle {
    font-size: 1.4rem;
    font-weight: 600;
    color: #eee;
}

$resultSubjectsArea-width: 80%;
$resultSubjects-marginLeftandRight: (
    100%-$resultSubjectsArea-width)/2;

.resultSubjectsArea {
    margin: 0 $resultSubjects-marginLeftandRight;
    margin-top: 0.2rem;
    width: $resultSubjectsArea-width;
}

$resultSubjects-width: 1.2rem;

.resultSubjects {
    position: relative;
    border-bottom: solid 0.05rem #888;
    margin-top: 0.2rem;
    padding: 0 $resultSubjects-width;
    line-height: 2.6rem;
    font-size: 1.3rem;
    color: #f5f5f5;
}

.descriptionOfResultSubject {
    position: absolute;
    right: $resultSubjects-width;
    font-size: 0.78em;
    color: #bbb;
}