$font-color: #666;

body {
    color: $font-color;
}

.blur {
    filter: blur(0.1rem);
    transition: filter 1s;
}

.about-area {
    margin: 10rem auto;
    text-align: center;
}

.about-text {
    display: inline-block;
    margin: 1.5rem 0.8em;
    text-align: left;
    font-size: 0.85rem;
    color: #808080;
    line-height: 1.8em;
    letter-spacing: 0.1em;
}

.site-name-area {
    color: #555;
}

.site-name-ruby {
    letter-spacing: 0.5em;
}

.site-name {
    font-size: 4rem;
    font-weight: 600;
    letter-spacing: 0.01em;
}

.headline {
    margin: 4rem 0;
    font-size: 2rem;
}

footer {
    margin: 1rem 0;
}

.copyright {
    text-align: center;
    font-size: 0.6rem;
}