.subject-sentence {
    padding: 2rem 0;
    font-size: 2.2rem;
    color: #fff;
}

.answer-area {
    width: 100%;
}

.subject-answer {
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    font-size: 1.7rem;
    color: #e4e4e4;

    &::after {
        content: "ã€€";
        position: absolute;
        top: 0;
        right: 0;
        width: 2em;
        background: linear-gradient(to right, transparent, #333 90%);
    }
}

.pipe {
    border-left: solid 0.1rem #aaa;
}

.subject-answer-unmatched {
    color: #bbb;
    border-bottom: solid 0.05rem #888;
    background-color: #444;
}